<template>
  <div class="success">
<!--    <van-nav-bar left-arrow @click-left="goBack" title="访客通行码"></van-nav-bar>-->
    <navigation-bar :pageName="pageName" @onLeftClick="goBack">
    </navigation-bar>
    <div class="content">
      <div class="card" ref="cardDom">
        <!-- <div class="card-title">访客通行码</div> -->
        <div class="card-content">
          <div class="text">{{ addStr }}</div>
          <div class="text">有效期 {{ queryObj.startTimeStr }} 至 {{ queryObj.endTimeStr }}</div>
          <div class="pic">
            <div class="qr" v-show="queryObj.face==0">
              <canvas ref="QrcodeImg" class="imgCode"></canvas>
              <!-- <div class="pic-tips">
                二维码有效时间同访客有效期
              </div> -->
            </div>
            <div class="map">
              <div
                class="img map-img"
                @touchstart="gotouchstart($event)"
                @touchmove="gotouchmove($event)"
                @touchend="gotouchend"
                :style="getMapStyle"
              ></div>
              <div class="pic-tips">
                点击放大地图长按保存地图
              </div>
            </div>
          </div>
          <div class="info">
            <div class="item">
              <div class="title">到访人</div>
              <div class="val">{{ queryObj.guest }}</div>
            </div>
            <div class="item">
              <div class="title">是否人脸通行</div>
              <div class="val">{{ queryObj.face==1?'是':'否' }}</div>
            </div>
            <div class="item">
              <div class="title">是否有车辆</div>
              <div class="val">{{ queryObj.plateNum?'是':'否' }}</div>
            </div>
            <div class="item" v-if="queryObj.plateNum">
              <div class="title">车牌号</div>
              <div class="val">{{ queryObj.plateNum }}</div>
            </div>
          </div>
          <div class="tips">
            提示：访客可{{ queryObj.face==1?'刷脸':'使用二维码' }}进入小区门并乘坐电梯
          </div>
        </div>
      </div>
      <div class="btn-area">
        <button class="btn save-btn" @click="generatePicture">保存至相册</button>
        <!-- <button class="btn" @click="getCanvasImg">分享至微信</button> -->
      </div>
    </div>
    <img ref="mapImgDom" :src="getMapImg" alt />
    <van-image-preview v-model="showImg" :images="images" :showIndex="false"></van-image-preview>
  </div>
</template>

<script>
import { getQRInfo } from "xt/services/register";
import { ImagePreview, Toast } from "vant";
import { store } from "@/utils/store";
import QRCode from "qrcode"; //引入生成二维码插件
import html2canvas from "html2canvas";
import { storage } from "@/utils/store";
import NavigationBar from "../../../title/NavigationBar";
export default {
  name: "success",
  components: {
    NavigationBar,
    [ImagePreview.name]: ImagePreview
  },
  data() {
    return {
      pageName:"访客通行码",
      custNews: "",
      addArr: [],
      addStr: "",
      qrCanvas: "",
      qrcodeKey: "",
      queryObj: {},
      showImg: false, // 图片预览
      images: [storage.getStorage("xtBaseUrl") + "/map_xq.jpg"],
      dataURL: "", // 截图
      firstFlag: true,
      timeOutEvent: 0
    };
  },
  computed: {
    getMapImg() {
      return this.images[0];
    },
    getMapStyle() {
      const mapImg = storage.getStorage("xtBaseUrl") + "/map_xq.jpg";
      return {
        background: "url(" + mapImg + ")" + "no-repeat center",
        backgroundSize: "75%"
      };
    }
  },
  created() {
    this.queryObj = this.$route.query;
    this.custNews = store.get("custNews");
    this.addArr = this.queryObj.doorNum.split("-");
    this.addStr =
      this.custNews.AREA_NAME +
      this.addArr[0] +
      "号楼" +
      this.addArr[1] +
      "单元" +
      this.addArr[2];
  },
  mounted() {
    if (this.queryObj.face == 0) {
      // 没有人脸
      this.getQrcode();
    }
  },
  methods: {
    goBack() {
      this.$router.push({ path: "/visitorRegister/record" });
    },
    getQrcode() {
      this.qrCanvas = this.$refs.QrcodeImg;
      this.loading = true;
      let that = this;
      let data = {
        areaCode: this.custNews.AREA_CODE,
        buildCode: this.addArr[0],
        unitCode: this.addArr[1],
        roomCode: this.addArr[2],
        ownerCode: "",
        ownerName: this.queryObj.guest,
        phone: this.queryObj.guestPhone,
        endTime: this.queryObj.endTime,
        appFaceParkingId: this.queryObj.id
      };

      getQRInfo(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.qrcodeKey = res.data;
            if (that.qrcodeKey != "") {
              QRCode.toCanvas(that.qrCanvas, that.qrcodeKey);
            } else {
              that.$toast("获取二维码失败");
            }
          } else {
            console.log("获取二维码");
            that.$toast(res.message);
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    generatePicture() {
      let cardDom = this.$refs.cardDom;
      let width = cardDom.offsetWidth;
      let height = cardDom.offsetHeight;
      html2canvas(cardDom, {
        scale: 2,
        width,
        height,
        useCORS: true // 【重要】开启跨域配置
      }).then(canvas => {
        this.onDeviceReady(canvas);
      });
    },
    onDeviceReady(canvas) {
      console.log('打印插件 '+ window.canvas2ImagePlugin)
      if (window.cordova) {
         // 自定义加载图标
        this.$toast.loading({
          duration: 0,
          message: "加载中...",
          forbidClick: true,
          loadingType: "spinner"
        });
        window.canvas2ImagePlugin.saveImageDataToLibrary(
          msg => {
            this.$toast.clear();
            console.log(JSON.stringify(msg));
            this.$toast("图片保存成功");
          },
          err => {
            this.$toast.clear();
            console.log(JSON.stringify(err));
            this.$toast("图片保存失败-请检查权限是否打开");
          },
          canvas
          // "jpeg" // format is optional, defaults to 'png'
        );
      }
    },
    // 保存图片
    img2Cnvas() {
      let cardDom = this.$refs.mapImgDom;
      let width = cardDom.offsetWidth;
      let height = cardDom.offsetHeight;
      html2canvas(cardDom, {
        scale: 2,
        width,
        height,
        useCORS: true, //（图片跨域相关）
        allowTaint: false //允许跨域（图片跨域相关）
      }).then(canvas => {
        // 保存到相册
        this.onDeviceReady(canvas);
      });
    },
    gotouchstart(e) {
      console.log("--tst", e);
      let ty = e.touches[0].clientY;
      console.log("---touchStart: " + ty);
      let that = this;
      clearTimeout(this.timeOutEvent); //清除定时器
      this.timeOutEvent = setTimeout(function() {
        that.timeOutEvent = 0;
        that.img2Cnvas();
        console.log("--gotouchstart--");
      }, 1000); //这里设置定时
      this.baseClientY = e.touches[0].clientY;
    },
    //手释放，如果在500毫秒内就释放，则取消长按事件，此时可以执行onclick应该执行的事件
    gotouchend() {
      clearTimeout(this.timeOutEvent);
      if (this.timeOutEvent != 0) {
        //这里写要执行的内容（尤如onclick事件）
        this.showImg = true;
      }
    },
    //如果手指有移动，则取消所有事件，此时说明用户只是要移动而不是长按
    gotouchmove(e) {
      console.log("---touchMove: " + e.touches[0].clientY);
      let touchY = e.touches[0].clientY;
      let diff = Math.abs(touchY - this.baseClientY);
      console.log("---touchDiff: " + diff);
      if (diff < 10) {
        return;
      }
      clearTimeout(this.timeOutEvent); //清除定时器
      this.timeOutEvent = 0;
    }
  }
};
</script>

